
















































































.view {
  display: flex;
  flex-direction: column;
  height: calc(100vh - 100px);
}

.tag-selector {
  margin: 20px 0;
}

.data-view {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  height: 32px;
  margin: 0 40px;

  .name {
    width: 180px;
    text-align: left;
    margin-right: auto;
  }

  .tags {
    .tag.selected {
      color: red;
      font-weight: bold;
    }
  }
}
